    <el-row>
      <el-col :span="12" style="padding: 10px;border-radius: 5px;">
        <div style="border: 1px solid #ccc;padding: 5px;border-radius: 5px;">
          <div style="width: 100%;height: 400px;overflow: scroll">
            <p style="font-weight: bold;font-size: 18px;color: #000;margin: 0;padding-left: 5px">销量排行</p>
            <ul v-if="productList.length" style="padding: 0 12px;">
              <li style="border: 1px dashed #ccc;padding: 5px;margin-top: 10px;font-size: 12px" v-for="item in productList" :key="item.sale_times">{{item.name}}</li>
            </ul>
            <div v-else style="text-align: center;line-height: 200px">暂无数据</div>
          </div>
        </div>
      </el-col>
      <el-col :span="12" style="padding: 10px;border-radius: 5px;">
        <div style="border: 1px solid #ccc;padding: 5px;border-radius: 5px;">
          <div style="width: 100%;height: 400px">
            <p style="font-weight: bold;font-size: 18px;color: #000;margin: 0;padding-left: 5px">流量来源</p>
            <section style="margin-top: 10px" id="auth-button"></section>
            <section id="view-selector"></section>
            <section id="timeline"></section>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
    export default {
        data() {
            return {
                totals: [],
                orderList: [],
                productList: []
            }
        },
        methods: {
            getData() {
                this.$post('home').then(res => {
                    this.totals = res.totalSales.list.map(item => {
                        return item.total_sale
                    });
                    this.orderList = res.totalOrders.list.map(item => {
                        return item.total_order
                    });
                    this.productList = res.top_products;
                    if (res.totalOrders.list.length) {
                        this.drawLine();
                        this.drawLine1();
                    }
                })
            },
            drawLine() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '总销售额 $366'},
                    xAxis: {
                        type: 'category',
                        data: ['00:00', '01:00', '02:00', '03:00', '04:00', '05:00', '06:00', '07:00', '08:00', '09:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00', '22:00', '23:00', '24:00']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: this.totals,
                        type: 'line'
                    }]
                });
            },
            drawLine1() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart1'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '总订单数 34单'},
                    xAxis: {
                        type: 'category',
                        data: ['00:00', '01:00', '02:00', '03:00', '04:00', '05:00', '06:00', '07:00', '08:00', '09:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00', '22:00', '23:00', '24:00']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: this.orderList,
                        type: 'line'
                    }]
                });
            },
            drawLine2() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart2'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '流量 0回话 0访客'},
                    xAxis: {
                        type: 'category',
                        data: ['00:00', '02:00', '04:00', '06:00', '08:00', '10:00', '12:00', '14:00', '16:00', '18:00', '20:00', '22:00', '24:00']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [820, 221, 654, 865, 543, 564, 1330, 984, 566, 1330, 984, 566],
                        type: 'line'
                    }]
                });
            },
            drawLine3() {
                gapi.analytics.ready(function() {
                    var CLIENT_ID = '628874914398-uqthhdqsb0sv06s1sork5j1uf4k69555.apps.googleusercontent.com';
                    gapi.analytics.auth.authorize({
                        container: 'auth-button',
                        clientid: CLIENT_ID,
                    });
                    var viewSelector = new gapi.analytics.ViewSelector({
                        container: 'view-selector'
                    });
                    var timeline = new gapi.analytics.googleCharts.DataChart({
                        reportType: 'ga',
                        query: {
                            'dimensions': 'ga:source',
                            'metrics': 'ga:organicSearches',
                            'start-date': '30daysAgo',
                            'end-date': 'yesterday',
                        },
                        chart: {
                            type: 'PIE',
                            container: 'timeline'
                        }
                    });


                    gapi.analytics.auth.on('success', function(response) {
                        viewSelector.execute();
                    });
                    viewSelector.on('change', function(ids) {
                        var newIds = {
                            query: {
                                ids: ids
                            }
                        };
                        timeline.set(newIds).execute();
                    });
                });
            }
        },
        mounted() {
            this.getData();
            this.drawLine2();
            this.drawLine3();
        }
    }
</script>

<style lang="scss" scoped>
  /deep/ #view-selector table{
    margin-top: 10px;
    width: 33%!important;
    display: inline-block;
  }
  /deep/ #view-selector table .shr-a-shr-fc-shr-fb-shr-cb-shr-pe{
    width: 70px!important;
  }
  /deep/ #view-selector table td:first-child {
    width: 50px;
  }
</style>



